import React from 'react'
import Table from 'react-table'

const ReactTable = (props) => {
  return (
    <Table
      previousText='Previous'
      nextText='Next'
      noDataText='No rows found'
      pageText='Page'
      ofText='of'
      rowsText='rows'
      {...props}
    />
  )
}

export default ReactTable
