export const getExternalAppUrl = (appId, url) => {
  switch (appId) {
    case AppId.APP_NO1:
    case AppId.DIVAY_APP:
      return `${window.location.origin}/${url}?redirectUrl=${url}`
    case AppId.INSURANCE_APP:
    case AppId.DIVAY_INSURANCE_APP:
      return `${window.location.origin}/insurance${url}?redirectUrl=${url}`
    case AppId.SUPPLEMENT_APP:
    case AppId.DIVAY_SUPPLEMENT_APP:
      return `${window.location.origin}/supplement${url}?redirectUrl=${url}`
    case AppId.ELITE_APP:
      return `${window.location.origin}/elite${url}?redirectUrl=${url}`
  }
}


export const getContextPath = (appId) => {
  switch (appId) {
    case AppId.APP_NO1:
    case AppId.DIVAY_APP:
      return ''
    case AppId.INSURANCE_APP:
    case AppId.DIVAY_INSURANCE_APP:
      return 'insurance'
    case AppId.SUPPLEMENT_APP:
    case AppId.DIVAY_SUPPLEMENT_APP:
      return 'supplement'
    case AppId.ELITE_APP:
      return 'elite'
  }
}

export const getPropObject = (obj, prop) => {
  if (!obj) {
    return null
  }
  return prop.split('.').reduce((r, e) => {
    return r ? r[e] : null
  }, obj)
}


