import React from 'react'
import { Provider } from 'react-redux'
import { createStore, applyMiddleware, compose } from 'redux'
import createDebounce from 'redux-debounced'
import thunk from 'redux-thunk'
import { PersistGate } from 'redux-persist/integration/react'
import { persistStore } from 'redux-persist'
import { setUpHttpClient } from './utility/HttpClient'
import rootReducer from './redux/reducers/rootReducer'
import AppRouter from './routes/AppRouter'
import LoadingSpinner from './components/Loading-spinner'
import './components/RippleButton'
import * as serviceWorker from './serviceWorker'
import './index.scss'
import 'react-perfect-scrollbar/dist/css/styles.css'
import 'react-toastify/dist/ReactToastify.css'
import 'prismjs/themes/prism-tomorrow.css'
import { setBaseHistory } from './history'

const App = ({ children, appId, appReducer, message, apiBaseUrl, history }) => {
  const middlewares = [thunk, createDebounce()]
  const composeEnhancers =
    window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose

  const store = createStore(
    rootReducer(appReducer),
    {},
    composeEnhancers(applyMiddleware(...middlewares))
  )


  const persistor = persistStore(store)

  setBaseHistory(history)

  setUpHttpClient(store, apiBaseUrl)

  return (
    <Provider store={store}>
      <PersistGate loading={null} persistor={persistor}>
        <LoadingSpinner />
        <AppRouter
          message={message}
          appId={appId}
          history={history}
          children={children}
        />
      </PersistGate>
    </Provider>
  )
}

serviceWorker.unregister()

export default App
